import React, { useEffect, useState } from 'react'
import {
    CRow,
    CCol,
    CCard,
    CCardHeader,
    CCardBody,
    CLabel,
    CInput,
    CInputGroup,
    CButton,
} from '@coreui/react'

import { ColorPicker, useColor, toColor } from 'react-color-palette'
import "react-color-palette/lib/css/styles.css"
import { rgbToHex, hsvToHex } from "../util/colorConversion"


const ColorConverter = () => {
    const [color, setColor] = useColor("hex", "#121212");
    const [hex, setHEX] = useState("")
    const [r, setR] = useState("")
    const [g, setG] = useState("")
    const [b, setB] = useState("")
    const [h, setH] = useState("")
    const [s, setS] = useState("")
    const [v, setV] = useState("")

    const setColorHEX = (e) => {
        var data = e.target && e.target.value;
        setHEX(data);

        if (data && data.length === 7) {
            setColor(toColor("hex", data));
        }
    }

    const setColorRGB = (nr, ng, nb) => {
        if (!nr || nr < 0) nr = 0;
        if (!ng || ng < 0) ng = 0;
        if (!nb || nb < 0) nb = 0;
        if (nr > 255) nr = 255;
        if (ng > 255) ng = 255;
        if (nb > 255) nb = 255;

        setR(nr);
        setG(ng);
        setB(nb);

        var tempHex = rgbToHex(nr, ng, nb)
        setColor(toColor("hex", tempHex));
    }

    const setColorHSV = (nh, ns, nv) => {
        console.log(nh, ns, nv);

        if (!nh || nh < 0) nh = 0;
        if (!ns || ns < 0) ns = 0;
        if (!nv || nv < 0) nv = 0;
        if (nh > 359) nh = 359;
        if (ns > 100) ns = 100;
        if (nv > 100) nv = 100;
        setH(nh);
        setS(ns);
        setV(nv);

        var tempHex = hsvToHex(nh, ns, nv)
        setColor(toColor("hex", tempHex));
    }

    useEffect(() => {
        setHEX(color.hex);
        setH(color.hsv.h);
        setS(color.hsv.s);
        setV(color.hsv.v);
        setR(color.rgb.r);
        setG(color.rgb.g);
        setB(color.rgb.b);
    }, [color])

    return (
        <CRow>
            <CCol lg={12}>
                <CCard>
                    <CCardBody>
                        <CRow>
                            <CCol lg={8}>
                                <CCard key='color_converter' style={{ marginBottom: 0 }} >
                                    <CCardHeader>
                                        Color Converter
                                    </CCardHeader>
                                    <CCardBody>
                                        <CRow style={{ padding: '1rem 0 1rem 0', lineHeight: '35px' }}>
                                            <CCol md="2">
                                                <CLabel><b> HEX </b></CLabel>
                                            </CCol>
                                            <CCol xs="12" md="10">
                                                <CInputGroup>
                                                    <CInput id="hex" value={hex} onChange={setColorHEX}></CInput>
                                                    <CButton color="primary">Copy</CButton>
                                                </CInputGroup>
                                            </CCol>
                                        </CRow>
                                        <CRow style={{ padding: '1rem 0 1rem 0', lineHeight: '35px' }}>
                                            <CCol md="2">
                                                <CLabel><b> RGB </b></CLabel>
                                            </CCol>
                                            <CCol xs="12" md="10">
                                                <CInputGroup>
                                                    <CInput id="rgb_r" type='number' value={r} onChange={(e) => { setColorRGB(e.target.value, color.rgb.g, color.rgb.b) }}></CInput>
                                                    <CInput id="rgb_g" type='number' value={g} onChange={(e) => { setColorRGB(color.rgb.r, e.target.value, color.rgb.b) }}></CInput>
                                                    <CInput id="rgb_b" type='number' value={b} onChange={(e) => { setColorRGB(color.rgb.r, color.rgb.g, e.target.value) }}></CInput>
                                                    <CButton color="primary">Copy</CButton>
                                                </CInputGroup>
                                            </CCol>
                                        </CRow>
                                        <CRow style={{ padding: '1rem 0 1rem 0', lineHeight: '35px' }}>
                                            <CCol md="2">
                                                <CLabel><b> HSV </b></CLabel>
                                            </CCol>
                                            <CCol xs="12" md="10">
                                                <CInputGroup>
                                                    <CInput id="hsv_h" disabled value={h} onChange={(e) => { setColorHSV(e.target.value, color.hsv.s, color.hsv.v) }}></CInput>
                                                    <CInput id="hsv_s" disabled value={s} onChange={(e) => { setColorHSV(color.hsv.h, e.target.value, color.hsv.v) }}></CInput>
                                                    <CInput id="hsv_v" disabled value={v} onChange={(e) => { setColorHSV(color.hsv.h, color.hsv.s, e.target.value) }}></CInput>
                                                    <CButton color="primary">Copy</CButton>
                                                </CInputGroup>
                                            </CCol>
                                        </CRow>
                                    </CCardBody>
                                </CCard>
                            </CCol>
                            <CCol lg={4} style={{ display: 'flex', justifyContent: 'space-around' }}>
                                <ColorPicker width={400} height={256} color={color} onChange={setColor} hideHSV hideRGB hideHEX dark />
                            </CCol>
                        </CRow>
                    </CCardBody>
                </CCard>
            </CCol>
        </CRow>
    )
}

export default ColorConverter
